<template>
  <div class="position-fixed top-0 start-0 end-0 bottom-0 bg-light">
        <div id="container" class="h-100"></div>
        <div class="position-fixed top-0 start-0 end-0  bg-white row align-items-center p-1">
            <div class="col-auto" @click="$emit('getinfo',{type:0})">
                <span class="fs-4 bi bi-chevron-left"></span>
            </div>
            <div class="col">
                <div class="pt-1 text-center fs-5">选址</div>
            </div>
            <div class="col-auto">
            </div>
        </div>
        <div class="position-fixed bottom-0 start-50 translate-middle bg-white rounded p-2 mb-5" style="width: 85%;">
            <div class="row">
                <div class="col">{{form.name}}</div>
                <div class="col-auto">
                    <button class="btn btn-primary" @click="onsubmit">确定</button>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
export default {
  props:{
    data:{}
  },
  mounted(){
    window.ysdt._this=this;
    window.ysdt.PositionPicker(this);
  },
  data(){
      return {
          form:'',
      }

  },
  methods:{
      onsubmit(){
          this.$emit('getinfo',{type:1,data:{url:this.data.url,data:this.form}});
      },
      getnamedata(key){
          this.form=key;
      },
      nogeolocation(){

      }
  }
}
</script>

<style>

</style>


